import React, { Component, PropTypes } from 'react';
import KlassName from 'teapan/klassName';
import { SKINS } from '../../constant/props';

import Warpper from '../warpper/Warpper';

class Necker extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			skin,
			className,
			children,
			...others
		} = props;

		const fatherKlassName = KlassName('layout-necker',{
			['layout-necker--' + skin]:skin,
		},className);

		const klassName = KlassName('necker',className);

		return (
			<div className={fatherKlassName}>
				<Warpper>
					<div {...others} className={klassName}>
						{children}
					</div>
				</Warpper>
			</div>
		);
	}
};

Necker.propTypes = {
	skin: PropTypes.oneOf(SKINS),
	className: PropTypes.string,
	children: PropTypes.node,
};

Necker.defaultProps = {
};

export default Necker;